<template>
  <div>
    <!-- <h1>MainComponent - 테스트 소개 페이지</h1> -->
    <!-- <p>어쩌구 저쩌구 테스트</p> -->
    <button @click="$emit('changePage',1)" class="main-button">테스트 시작하기</button>
    <br>
    <button class="kakao-share" @click="kakaoShare">카카오 공유하기</button>

  </div>
</template>

<script>
export default {
    name: 'MainComponent',
    // props를사실 안써도 된다. page라는 변수자체를 쓰질 않음 그냥 emit만써도 되는구나
    // route써도되지만 뭔가 신기하니 이걸로 해보자 우선 여러 개량을 거쳐보기
    props:{
    page:Number,
  },
  methods:{
    kakaoShare() {
      window.Kakao.Share.sendCustom({
        templateId: 93225,
        // 카카오톡이 설치 되지 않았을때 마켓으로 이동
        installTalk: true
      });},
  }

    }

</script>

<style>
.main-button{
    padding:15px;
    font-size:20px;
    background: #eee;
    border:none;
    border-radius: 10px;
    cursor: pointer;
    margin-bottom: 10px;
    /* position:absolute; */
    /* bottom:20px;
    right: 20px; */
}

.main-button:hover{
  background:rgb(187, 187, 187);
}
.kakao-share {
  padding:15px;
  font-size:20px;
  border:none;
  border-radius: 10px;
  cursor: pointer;
  color: black;
  text-align: center;
  background: #ffe812;

}
.kakao-share:hover{
  background: rgb(224, 230, 146);

}
</style>